
/* Regular page: */

h1, h2, h3 {
  font-family: 'Fira Sans Boold';
  color: #222222;
}

#pageData {
  position: absolute;
  top: 80px;
  left: 0;
  width: 100%;
}

/* Slider: */

#slider {
  height: 300px;
  width: 100%;
  background: url('/images/slide-image.jpg') no-repeat center center / cover;
}

#credentials {
  width: 100%;
  text-align: center;
  padding-top: 100px;
}

/* Article! */

#article {
  margin-top: 220px;
  background: white;
  padding: 30px 40px 50px;
  color: dimgrey;
}

#article .holder {
  max-width: 1024px;
  margin: 0 auto;
}

#article h1 {
  margin: 0 0 25px 0;
  text-align: left;
}

#article .body {
  font-size: 0.8em;
  font-family: 'Fira Sans UltraLight';
  text-align: left;
}

#article ul {
  display: inline-block;
  text-align: left;
}

/* Footer! */

#footer {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#5e5e5e+0,5e5e5e+0,3f3f3f+47,5e5e5e+100 */
  background: rgb(94,94,94); /* Old browsers */
  background: -moz-linear-gradient(-45deg,  rgba(94,94,94,1) 0%, rgba(94,94,94,1) 0%, rgba(63,63,63,1) 47%, rgba(94,94,94,1) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg,  rgba(94,94,94,1) 0%,rgba(94,94,94,1) 0%,rgba(63,63,63,1) 47%,rgba(94,94,94,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg,  rgba(94,94,94,1) 0%,rgba(94,94,94,1) 0%,rgba(63,63,63,1) 47%,rgba(94,94,94,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5e5e5e', endColorstr='#5e5e5e',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  color: white;
}

#footer a {
  transition: 0.3s;
}

#footer .phones a {
  color: silver;
}

#footer .phones a:hover {
  color: whitesmoke;
}

#footer .address {
  border-right: 1px solid white;
}

#footer #author a {
  color: silver;
  border-bottom: 1px dotted whitesmoke;
}

#footer #author:hover a {
  color: whitesmoke;
}